/* Navigation.css */


.sidebar {
    position: fixed;
    top: 0px;
    Right: -400px;
    width: 400px;
    height: 100%;
    background-color:#fffffeee;
    /*outline-color: #c4e9fff0;*/
    transition: right 0.3s ease;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1); /* サイドバーに影を追加 */
    overflow-y: auto;
  }
  
  .sidebar.open {
    right: 0;
  }
  
  .sidebar-list {
    padding: 0px;
    overflow: auto; /* または overflow: scroll; */
  }

  button.close-button {
    top: 16px;
    right: 16px;
    background-color: #363636;
    color: #fff;
    padding: 8px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
  }
  
  button.toggle-button {
    font-family: 'Courier New', monospace;
    position: fixed;
    font-size: 20px;
    top: 30px;
    right: 20px;
    transform: translateY(-50%);
    background-color: #58aaff;
    color: #ffffff;
    padding-left: 6px;
    padding-right: 6px;
    cursor: pointer;
    border: none;
    writing-mode: horizontal-tb;
    white-space: nowrap;
    z-index: 2;
  }
  
  /* 戻るボタン */
  .button-return {
    font-size: 16px;
    position: fixed ; /* 絶対位置指定 */
    top: 18px; /* 上からの位置 */
    right: 55px; /* 左からの位置 */
    text-align: left; /* テキストを左寄せにする */
    /*padding: 8px; /* ボタンの余白を調整 */
    background-color: #00000000;
    /*color: #ffffff; /* ボタンのテキスト色 */
    border: none; /* ボーダーをなくす */
    cursor: pointer;
  }
  .button-return:hover {
    color: #006698;
  }

  /* ホームボタン */
  .button-home {
    font-size: 25px;
    position: fixed; /* 絶対位置指定 */
    top: 10px; /* 上からの位置 */
    left: 50px; /* 左からの位置 */
    text-align: left; /* テキストを左寄せにする */
    /*padding: 8px; /* ボタンの余白を調整 */
    background-color: #00000000;
    /*color: #ffffff; /* ボタンのテキスト色 */
    border: none; /* ボーダーをなくす */
    cursor: pointer;
  }

  /* 授業回名 */
  .Lesson-text {
    position: relative;
    top: 55px;
    left: 15px;
    color: #646566;
    font-size: 16px;
    /*margin-left: 8px;*/
    /*margin-bottom: 20px;*/
    text-align: left; /* テキストを左寄せにする */
    white-space: nowrap; /* テキストが横に改行されないようにする */
    z-index: 2;
  }

/* 半透明のボックス */
.overlay-box {
  position: fixed;
  top: 0px; /* タイトルの下に表示される位置を調整 */
  left: 0;
  width: 300px;
  height: 50px; /* 適宜調整 */
  background-color:#f2f2f2;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* サイドバーに影を追加 */
  z-index: 1; /* タイトルより手前に表示する */
}

  /* サイドバー内のアイテムスタイル */
 /* サイドバー全体 */
.navigator-style{
  ul {
    list-style-type: none;
    padding-left: 10px;
    padding-top: 60px;
    margin: 0;
  }
  
  /* リスト項目 */
  li {
    /*font-family: 'Courier New', monospace;*/
    padding-top: 4px;
    padding-bottom: 6px;
    padding-left: 0px;
    border-bottom: 1px solid #c4e9fff0;
    text-align: left;
  }
  
  /* テキストリンク */
  li button {
    font-size: 15px;
    border: none;
    outline-offset: 4px; /* アウトラインからボタンのコンテンツまでの距離 */
    text-align: left; /* ボタンを左揃えにする */
    transition: background-color 0.3s ease; /* 背景色変更のアニメーション効果を追加 */
    background-color: #00000000;
  }
  
  /* マウスオーバー時のスタイル */
  li button:hover {
    color: #006698;
    
  }
  
  /* クリック時のスタイル */
  li button:active {
    background-color: #8f8f8f00;
  }

  li:last-child {
    border-bottom: none;
  }
}