.common-style {
    /* 共通のスタイルを定義 */

    body {
      background-color: #f8f9fa;
      text-align: left;
    }
    
    .common-style {
      /*font-family: 'Courier New', monospace;*/
      color: #343a40;
      /*max-width: 90%;*/
      /*margin: 0 auto;*/
      /*overflow-y: auto;*/
      text-align: left;
    }
    
    h1 {
      color: #8dacf9;
      text-align: left;
      margin-top: 0em;
      margin-bottom: 0em;
    }

    h2 {
      color: #3672f6;
      text-align: left;
      margin-top: 2em;
      margin-bottom: 0.5em;
    }

    h3 {
      color: #006698;
      text-align: left;
      margin-top: 1.5em;
      margin-bottom: 0.5em;
    }

    h4 {
      color: #2433a4;
      text-align: left;
      margin-top: 1em;
      margin-bottom: 0.3em;
    }
    
    /* リンク */
    a {
      color: #007bff;
      text-decoration: none;
    }
    
    a:hover {
      text-decoration: underline;
    }
    
    /* 段落 */
    p {
      text-align: left;
      /*padding-left: 0.0em;*/
      margin-bottom: 1em;
    }

    /* リスト要素のスタイル */
    ul {
      margin: 0; /* リストの外側の余白 */
      /*padding-left: 1em;*/ /* リストの内側の余白 */
      /*padding-top: 0em;*/
      list-style-type: disc; /* マーカーが点になる */
      /*font-size: 22px;*/
      /*font-family: 'Courier New', monospace;*/
    }
    /* 子リストのスタイル */
    ul ul {
      list-style-type: circle; /* 子リストのマーカーを四角形に設定 */
      margin-top: 0em; /* 子リストの上側の余白を設定 */
      margin-bottom: 0em; /* 子リストの下側の余白を設定 */
      margin-left: 0em; /* 子リストの左側の余白を設定 */
      /*padding: 0em;*/
    }

    /* 番号付きリスト要素のスタイル */
    ol {
      margin: 0; /* リストの外側の余白 */
      padding-left: 1.5em; /* リストの内側の余白 */
      /*padding-top: 0em;*/
    }
    ol li {
      padding-left :0em;
    }

    /* リストアイテム要素のスタイル */
    li {
      /*margin: 0.3em 0;*/ /* アイテム間の上下の余白 */
      /*padding: 0;*/ /* アイテムの内側の余白 */
    }

    /* テーブル全体のスタイル */
    table {
      /*width: 50%;*/
      border-collapse: collapse;
      border: 1px solid #ddd;
    }

    /* テーブルの見出しセルのスタイル */
    th {
      background-color: #ffffff;
      border: 1px solid #ddd;
      padding-top: 8px;
      padding-bottom: 8px;
      padding-left: 8px;
      padding-right: 8px;
      text-align: center;
      white-space: nowrap;
    }

    /* テーブルのデータセルのスタイル */
    td {
      border: 1px solid #ddd;
      padding-top: 8px;
      padding-bottom: 8px;
      padding-left: 8px;
      padding-right: 8px;
      text-align: left;
      white-space: nowrap;
    }

    /* 奇数行の背景色を変更する場合 */
    tr:nth-child(odd) {
      background-color: #f2f2f2;
    }
  }

  .scroll-link {
    cursor: pointer; /* ポインターを表示してクリッカブルであることを示す */
    /* 他のスタイルプロパティ */
  }

 /**
 * p5カンバス 
 * 
 */

  .sketch-canvas{
    margin-top: 50px;
    margin-bottom: 5px;
  }

  /**
 * コード 
 * 
 */

  .code-block {
    font-family: 'Courier New', monospace;
    overflow-x: auto;
    overflow-y: hidden; /* スクロールバーを非表示にする */
    max-height: 500px; /* 適切な高さに設定してください */
    border: 0px solid; /* Processingエディタに近い色に設定 */
    border-color:#5b5b5b;
    border-radius: 4px;
    /*background-color: #f5f5f5;*/ /* Processingエディタに近い背景色に設定 */
    background-color: #fafaf9;
    padding-bottom: 10px;
    position: relative; /* グラデーションの影を設定するための相対位置指定 */
    margin-top: 5px;
    margin-bottom: 10px;
  }

  /* 折り畳み中のコードブロックの下部にグラデーションの影を追加 */
  .code-block.collapsed::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 10px; /* グラデーションの影の高さ */
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1)); /* グラデーションの影の設定 */
  }

  .code-block.plane {
  }

  .code-block pre {
    margin: 0;
    text-align: left;
    padding-top: 0px;
    padding-bottom: 10px;
    padding-right: 10px;
    padding-left: 10px;
  }
  
  .code-block pre code {
    font-family: 'Courier New', monospace;
    font-size: 16px;
    color: #000000; /* Processingエディタに近いテキスト色に設定 */
  }
    
  /* Processingのビルトイン関数 */
  .code-block pre .hljs-built_in {
    color: #026799;
  }

  /* Processingの型 */
  .code-block pre .hljs-type {
    color: #e87029;
  }

  /* 関数やメソッド */
  .code-block pre .hljs-function {
    color: #d35400;
  }

  /* Processingの予約語
      void, for, if 
   */
  .code-block pre .hljs-keyword {
    color: #638a0f;
  }

  /* コメント */
  .code-block pre .hljs-comment {
    color: #646566;
  }
  
  /* 変数や関数 */
  .code-block pre .hljs-name {
    color: #000000;
  }
  
  /* 文字列 */
  .code-block pre .hljs-string {
    color: #0d0810;
  }
  
  /* 数値 */
  .code-block pre .hljs-number {
    color: #000000;
  }

/* コードボタン類 */
.code-buttons{
  text-align: left;
}

/* コード折り畳みトグルボタン */
.code-toggle-button {
  font-size: 14px;
  left: 0px;
  background-color: #51ca6e;
  color: #fff;
  border: none;
  /*padding: 8px 16px;*/
  margin-right: 2px;
  cursor: pointer;
  border-radius: 4px;
  transform: translateY(0%);
}

.toggle-button:hover {
  background-color: #218838;
}

  /* コピーボタン */
.copy-button {
  font-size: 14px;
  background-color: #58aaff;
  color: #fff;
  border: none;
  /*position:absolute;*/
  right: 0px;
  /*padding: 4px 10px;*/
  cursor: pointer;
  border-radius: 4px;
  margin-bottom: 10px;
  
  transform: translateY(0%);
}

.copy-button:hover {
  background-color: #214b72;
}